/*===========================================================================
	CSS 초기화
============================================================================*/
* {font-family: 'Pretendard', 'Open Sans' ,sans-serif; box-sizing:border-box; margin:0; padding:0; font-weight: normal;}
html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, em, img, strong, b, i, dl, dt, dd, ol, ul, li, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, footer, header, menu, nav, section, video, a {border:0; outline:0; font-size:100%; color:#333; line-height:normal;}
body {line-height:normal;}
article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section {display:block;}
ul, ol, li {list-style:none;}
strong {display: inline-block;}
a {margin:0; padding:0; font-size:100%; }
table {border-collapse:collapse; border-spacing:0;}
input, select {vertical-align:middle;}
p {line-height:1.5; word-break: keep-all;}
img {width: 100%}
.quill img { width: unset; }
input {outline:none; }
input[type="text"] { border-radius: 2px; border: solid 1px #d9d9d9; }
input[type="text"]::placeholder { color: rgba(0, 0, 0, 0.25); }
input[type="number"] { border-radius: 2px; border: solid 1px #d9d9d9; }
input[type="number"]::placeholder { color: rgba(0, 0, 0, 0.25); }

textarea { border-radius: 2px; border: solid 1px #d9d9d9; padding: 12px; outline: unset; }
textarea::placeholder { color: rgba(0, 0, 0, 0.25); }

button {cursor:pointer; border: 0; outline: 0; background: none; font-size: 16px;}
a {text-decoration: none;}
textarea {resize: none;}
.col-group {display:flex;}
.container {max-width: 1280px; height: auto; margin: 0 auto; position: relative;}
body{ -ms-overflow-style: none; } 
::-webkit-scrollbar { display: none; }

@media screen and (max-width:1280px) {
  .container{width: 100%; padding: 0 16px;}
}
