/* 로그인 페이지 */
.Login-page { width: 100vw; height: 100vh; background-image: url(./images/admin-login.png); background-position: center; background-size: cover; display: flex; justify-content: center; align-items: center; }
.Login-page .Login-box { width: 100%; max-width: 420px; border-radius: 10px; background-color: #fff; padding: 50px 80px 64px; }
.Login-page .logo-wrap { width: 132px; margin: 0 auto; margin-bottom: 28px; }
.Login-page .mant-p { font-weight: 300; letter-spacing: -0.4px; color: #a8a8a8; margin-bottom: 40px; }
.Login-page .Login-input-wrap { padding-top: 34px; border-top: 1px solid #c2c2c2; display: flex; flex-direction: column; gap: 27px; margin-bottom: 40px; }
.Login-page .Login-input-wrap p { font-size: 13px; color: rgba(51, 51, 51, 0.87); margin-bottom: 8px; font-weight: bold; }
.Login-page .Login-btn { width: 100%; height: 48px; border-radius: 5px; background-color: #1890ff; color: #fff; font-size: 14px; font-weight: bold; }

/* 관리자페이지 공통 */
/* White-box */
.White-box { width: 100%; padding: 32px; background-color: #fff; border-radius: 2px; }
/* 사이드 메뉴 바 */
.SMB { position: relative; }
.SMB .hd-wrap { position: absolute; top: 0; left: 0; width: 100%; height: 48px; box-shadow: 0 1px 4px 0 rgba(0, 21, 41, 0.12); background-color: #fff; display: flex; justify-content: flex-end; padding: 0 24px; }
.SMB .hd-wrap .user-wrap { display: flex; align-items: center; gap: 8px; }
.SMB .hd-wrap .user-wrap i { font-size: 32px; color: #005ac8; }
.SMB .hd-wrap .user-wrap .user-name { font-size: 14px; color: #000; }
.SMB .SMB-wrap { width: 208px; box-shadow: 2px 0 6px 0 rgba(0, 21, 41, 0.35); background-color: #001529; position: absolute; left: 0; top: 0; min-height: 100vh; }
.SMB .SMB-wrap .logo-wrap { width: 100%; height: 68px; display: flex; justify-content: center; align-items: center; }
.SMB .SMB-wrap .logo-wrap img { width: 85px; }
.SMB .SMB-wrap .ant-menu-title-content { color: #fff; }
.SMB .SMB-wrap .ant-menu-item { border-radius: unset; }
.SMB .SMB-wrap .ant-menu-item-selected { background-color: #1890ff; }
.SMB .SMB-wrap .ant-menu-item-icon { color: #fff; }
.SMB .SMB-wrap .ant-menu-submenu-arrow { color: #fff; }
.SMB .SMB-wrap .SMB-bt-wrap { position: absolute; bottom: 0; width: 100%; }
.SMB .SMB-wrap .SMB-bt-wrap > p { font-size: 12px; opacity: 0.4; color: #fff; line-height: 1.67; padding: 16px; }
.SMB .SMB-wrap .SMB-bt-wrap .Sign-out-bt { width: 100%; height: 40px; display: flex; align-items: center; gap: 16px; padding: 0 16px; background-color: #170101; }
.SMB .SMB-wrap .SMB-bt-wrap .Sign-out-bt i { color: #fff; }
.SMB .SMB-wrap .SMB-bt-wrap .Sign-out-bt span { color: #fff; font-size: 14px; }

/* 관리자 페이지 틀 */
.Manager-content { padding-left: 208px; padding-top: 48px; min-height: 100vh; background-color: #f5f5f5; }
.Manager-content .content-title-wrap { background-color: #fff; padding: 24px; height: 104px; display: flex; flex-direction: column; justify-content: space-between; }
.Manager-content .content-title-wrap .route-wrap { display: flex; gap: 20px; }
.Manager-content .content-title-wrap .route-wrap li { font-size: 14px; font-weight: 300; color: #8b8b8b; position: relative; }
.Manager-content .content-title-wrap .route-wrap li::after { position: absolute; left: calc(100% + 10px); content: "/"; font-size: 12px; }
.Manager-content .content-title-wrap .route-wrap li:last-child::after { display: none; }
.Manager-content .content-title-wrap .title-wrap { display: flex; justify-content: space-between; align-items: flex-end; }
.Manager-content .content-title-wrap .title-wrap h2 { font-size: 20px; color: rgba(0, 0, 0, 0.85); }
.Manager-content .content-title-wrap .Function-btn-wrap { display: flex; gap: 16px; }
.Manager-content .content-title-wrap .Registration-btn { display: flex; justify-content: center; align-items: center; gap: 8px; width: 82px; height: 32px; border-radius: 2px; background-color: #1890ff; color: #fff; font-size: 14px; }
.Manager-content .content-title-wrap .Registration-btn i { color: #fff; font-size: 14px; }
.Manager-content .content-title-wrap .Excel-Down-btn { display: flex; justify-content: center; align-items: center; gap: 8px; width: 82px; height: 32px; border-radius: 2px; background-color: #fff; border: solid 1px #1890ff; color: #1890ff; font-size: 14px; }
.Manager-content .content-title-wrap .Excel-Down-btn i { color: #1890ff; font-size: 14px; }
.Manager-content .content-wrap1 { height: calc(100vh - 104px - 48px); padding: 24px; overflow: auto; }

/* 페이지네이션 */
.ant-pagination { display: flex; justify-content: center; margin-bottom: 105px; }
.ant-pagination .ant-pagination-item { display: flex; justify-content: center; align-items: center; }
.ant-pagination .ant-pagination-item a { color: #aaa; }
.ant-pagination .ant-pagination-item-active { background-color: #1890ff; border-color: #1890ff; }
.ant-pagination .ant-pagination-item-active a { color: #fff; }
.ant-pagination .ant-pagination-options { display: none; }
/* 표 페이지네이션 */
/* .ant-pagination.ant-table-pagination { justify-content: center; } */

@media screen and (max-width:768px) {
    .ant-pagination { margin-bottom: 68px; flex-wrap: wrap; gap: 8px 0; }
}

/* 이미지가 있는 리스트 */
.img-list-wrap { display: flex; gap: 24px; flex-wrap: wrap; margin-bottom: 40px; }
.img-list-wrap > li { width: calc((100% - 72px) / 4); background-color: #fff; border-radius: 4px; overflow: hidden; }
.img-list-wrap > li .img-wrap { width: 100%; position: relative; }
.img-list-wrap > li .img-wrap::before { padding-top: 55.5%; display: block; content: ''; }
.img-list-wrap > li .img-wrap img { width: 100%; height: 100%; position: absolute; left: 0; top: 0; }
.img-list-wrap > li .title-wrap { padding: 24px; display: flex; flex-direction: column; gap: 8px; border: solid 1px #e9e9e9; }
.img-list-wrap > li .title-wrap .title-p { font-size: 17px; color: rgba(0, 0, 0, 0.85); }
.img-list-wrap > li .title-wrap .link-p { font-size: 15px; color: rgba(0, 0, 0, 0.45); }
.img-list-wrap > li .title-wrap .delete-btn-wrap { display: flex; gap: 20px; }
.img-list-wrap > li .title-wrap .delete-btn-wrap button { font-size: 14px; color: #1890ff; position: relative; }
.img-list-wrap > li .title-wrap .delete-btn-wrap button::after { position: absolute; content: '|'; left: calc(100% + 9px); color: #cccccc; }
.img-list-wrap > li .title-wrap .delete-btn-wrap button:last-child::after { display: none; }

/* 표 리스트 */
.table-wrap .table-btn-wrap button { font-size: 14px; color: #1890ff; }
.table-wrap .point-wrap { display: flex; align-items: center; gap: 8px; }
.table-wrap .blue-Point { display: flex; width: 6px; height: 6px; background-color: #1890ff; border-radius: 100%; }
.table-wrap .red-Point { display: flex; width: 6px; height: 6px; background-color: #fd5858; border-radius: 100%; }

/* 등록페이지 */
/* write 제목틀 */
.write-h2-wrap { display: flex; justify-content: space-between; align-items: flex-end; padding-bottom: 24px; border-bottom: 1px solid #c8c8c8; margin-bottom: 32px; }
.write-h2-wrap h2 { font-size: 18px; font-weight: bold; color: rgba(0, 0, 0, 0.85); }
.write-h2-wrap h2 span { font-size: 14px; color: rgba(0, 0, 0, 0.6); padding-left: 16px; }
.write-h2-wrap .Function-btn-wrap { display: flex; gap: 16px; align-items: flex-end; }
.write-h2-wrap .Function-btn-wrap .cancel-btn { width: 82px; height: 32px; font-size: 14px; border-radius: 2px; border: solid 1px #d9d9d9; color: rgba(0, 0, 0, 0.6); }
.write-h2-wrap .Function-btn-wrap .Save-btn { width: 82px; height: 32px; font-size: 14px; border-radius: 2px; background-color: #1890ff; color: #fff; }
.write-h2-wrap .Function-btn-wrap .investor-Add-btn { width: 104px; height: 32px; border-radius: 2px; background-color: #1890ff; font-size: 14px; color: #fff; }

/* input-틀 */
.write-list-wrap { display: flex; }
.write-list-wrap .write-list { width: 50%; }
.write-list { display: flex; flex-direction: column; gap: 24px; margin-bottom: 24px; }
.write-list > li { display: flex; }
.write-list > li .labal-p { width: 88px; font-size: 14px; color: rgba(0, 0, 0, 0.85); padding-top: 10px; padding-right: 16px; text-align: right; }
.write-list > li .input-wrap { width: calc(100% - 88px); min-height: 40px; display: flex; align-items: center; }
.write-list > li .input-box { width: 100%; position: relative; }
.write-list > li .input-box input { width: 100%; height: 40px; padding: 0 12px; }
.write-list > li .input-box input.grey-input { background-color: #f5f5f5; }
.write-list > li .input-box .Unit { position: absolute; right: 12px; top: 50%; transform: translateY(-50%); font-size: 14px; color: rgba(0, 0, 0, 0.65); }
.write-list > li .input-box textarea { width: 100%; }
.write-list > li .input-box .ant-picker { height: 40px; }
/* 주소input */
.write-list > li .input-box.Address { display: flex; gap: 8px; }
.write-list > li .input-box.Address input { width: 154px; }
.write-list > li .input-box.Address button { width: 120px; height: 40px; border-radius: 2px; background-color: #9a9a9a; font-size: 14px; color: #fff; }

.write-list > li .ant-upload-wrapper { width: 100%; }
.write-list > li .Editor-wrap { width: 100%; }
.write-list > li .Editor-wrap .ql-container { min-height: 400px; }

/* 배담금 인풋 */
.write-list > li .dividend-input-wrap { gap: 16px; }
.write-list > li .dividend-input-wrap .input-box { width: 450px; }
.write-list > li .dividend-input-wrap .dividend-btn { width: 104px; height: 40px; border-radius: 2px; background-color: #1890ff; color: #fff; font-size: 14px; }

/* 등록페이지 하단 저장버튼 */
.Save-btn-wrap { width: 100%; display: flex; justify-content: center; gap: 16px; }
.Save-btn-wrap button { width: 104px; height: 40px;  }
.Save-btn-wrap .cancel-btn { border-radius: 2px; border: solid 1px #d9d9d9; background-color: #fff; color: rgba(0, 0, 0, 0.6); }
.Save-btn-wrap .Save-btn { border-radius: 2px; background-color: #1890ff; color: #fff; }

/* 기본정보  Add-field-wrap */
.Add-field-wrap .ant-form-item { margin: unset; }
.Add-field-wrap .ant-space { align-items: center; }
.Add-field-wrap input { width: 450px; }
.Add-field-wrap .ant-btn-primary { width: 82px; height: 32px; margin-top: 24px; background-color: #1890ff; }
.Add-field-wrap .ant-btn-primary span { color: #fff; }

/* 마진추가 */
.margin-box { margin-bottom: 40px; padding-bottom: 40px; border-bottom: 1px solid #333; }


/* 배당 관리 ================== */
/* 공모정보 */
.contest-Information-wrap h2 { font-size: 24px; font-weight: bold; color: rgba(0, 0, 0, 0.85); margin-bottom: 32px; }
.contest-Information-wrap .Information-list { display: flex; flex-wrap: wrap; gap: 24px 0; }
.contest-Information-wrap .Information-list li { width: calc(100% / 4); display: flex; align-items: center; gap: 8px; }
.contest-Information-wrap .Information-list li .labal-p { font-size: 14px; font-weight: 500; color: rgba(0, 0, 0, 0.85); }
.contest-Information-wrap .Information-list li .answer-p { font-size: 14px; color: rgba(0, 0, 0, 0.65); }

/* 주소찾기 */
.DaumPostcode-wrap { position: fixed; z-index: 999; left: 50%; top: 50%; transform: translate(-50%, -50%); width: 500px; height: 460px; }
.DaumPostcode-wrap .close-wrap { height: 60px; background-color: #005ac8; display: flex; align-items: center; justify-content: space-between; padding: 0 16px; }
.DaumPostcode-wrap .close-wrap p { font-size: 20px; color: #fff; }
.write-list > li .input-box.Address .DaumPostcode-wrap .close-wrap .close-btn { width: auto; height: auto; background-color: unset; }
.write-list > li .input-box.Address .DaumPostcode-wrap .close-wrap .close-btn i { font-size: 24px; color: #fff; }

.Select-st { width: 300px; }
.contest-Information-wrap .Information-list.Information-list-st li { width: calc(100% /3); }

.blue-btn { color: #1890ff; }


/* 팝업 */
.White-box.pop-box { position: fixed; left: 50%; top: 50%; transform: translate(-50% , -50%); width: 500px; box-shadow: 0 0 20px 0 rgb(0 0 0 / 10%); }
.White-box.pop-box .write-list-wrap .write-list { width: 100%; }


